<template>
  <div :class="className" :style="{height:height,width:width}" class="wrapper" />
</template>

<script>
import echarts from './echarts' // echarts theme
import { mapGetters } from 'vuex'
import { debounce } from '@/utils'
require('echarts/theme/macarons')
const icons = {
  normal: require('@/assets/images/mapicon_normal_pin.png'),
  active: require('@/assets/images/mapicon_select_pin.png')
}
export default {
  props: {
    districtSelected: {
      type: String
    },
    className: {
      type: String,
      default: 'chart'
    },
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '100%'
    },
    stationOptions: {}
  },
  watch: {
    districtSelected: {
      handler (val) {
        val && this.setOption({ name: val })
      }
    },
    stationOptions: {
      deep: true,
      handler(val) {
        if (val) {
          const marks = val.map(mark => {
            return {
              _id: mark.id,
              _name: mark.name,
              coord: [mark.longitude, mark.latitude],
              symbol: mark.active ? 'image://' + icons.active : 'image://' + icons.normal
            }
          })
          this.setOption({ marks })
        }
      }
    }
  },
  data() {
    return {
      chart: null
    }
  },
  computed: {
    ...mapGetters('platform', ['selectedDistrict'])
  },
  mounted() {
    this.__chartOption = {
      series: [{
        type: 'map',
        map: this.selectedDistrict.name || '密云区',
        // animation: false,
        zoom: 1.1,
        selectedMode: 'single',
        itemStyle: {
          borderColor: '#499af5',
          borderWidth: 1,
          shadowColor: 'rgba(73, 154, 245, 0.9)',
          shadowBlur: 5,
          areaColor: '#35416b'
        },
        label: {
          color: '#badeff',
          fontSize: 14,
          show: true
        },
        emphasis: {
          itemStyle: {
            borderColor: '#499af5',
            borderWidth: 1,
            shadowColor: 'rgba(73, 154, 245, 0.9)',
            shadowBlur: 5,
            areaColor: '#35416b'
          },
          label: {
            color: '#badeff',
            fontSize: 14
          }
        },
        markPoint: {
          symbol: 'image://' + icons.normal,
          symbolSize: 50,
          data: [
            // {coord: [116.862476,40.486064], symbol: 'image://' + icons.active}
          ],
          animation: false
        }
      }]
    }
    this.$nextTick(() => {
      this.initChart()
    })
    this.__resizeHandler = debounce(() => {
      if (this.chart) {
        this.chart.resize()
      }
    }, 100)
    window.addEventListener('resize', this.__resizeHandler)
  },
  beforeDestroy() {
    if (!this.chart) {
      return
    }
    window.removeEventListener('resize', this.__resizeHandler)
    this.chart.dispose()
    this.chart = null
  },
  methods: {
    initChart() {
      this.chart = echarts.init(this.$el, 'macarons')
      this.chart.setOption(this.__chartOption)
      this.addMapHandler()
    },
    setOption({ name, marks }) {
      if (name) {
        this.__chartOption.series[0].map = name
      }
      if (marks) {
        this.__chartOption.series[0].markPoint.data = marks
      }
      this.chart && this.chart.setOption(this.__chartOption)
    },
    addMapHandler() {
      this.chart.on('click', params => {
        let { componentType, data } = params
        if (componentType === 'markPoint') {
          this.$emit('state-select', data._id)
        }
      })
    }
  }
}

</script>
