import { getDeptByHierarchy, queryStationListById } from '@/api/platform'
import Vue from 'vue'
const state = {
  districtOptions: [], // 区县列表
  selectedDistrictID: 0, // 选中区县id
  stationOptions: [], // 站点列表
  selectedStationID: 0 // 选中站点id
}

const mutations = {
  SET_PLATFORM_STATE(state, { key, value }) {
    if (state.hasOwnProperty(key)) {
      state[key] = value
    }
  },
  SET_SELECTEDDISTRICTID(state, id) {
    state.selectedDistrictID = id
  },
  SET_STATIONOPTIONS(state, { data }) {
    state.stationOptions = data
  },
  SET_SELECTEDSTATIONID(state, id) {
    state.selectedStationID = id
  },
  MARK_CURRENTSTATION(state, id) {
    state.stationOptions.forEach(option => Vue.set(option, 'active', false))
    state.stationOptions.forEach(option => {
      if (option.id === id) {
        Vue.set(option, 'active', true)
      }
    })
  }
}

const getters = {
  selectedDistrict: state => state.districtOptions.find(option => option.id === state.selectedDistrictID) || {}
}

const actions = {
  async getDeptByHierarchy({ commit, dispatch }) {
    const data = (await getDeptByHierarchy()).data
    commit('SET_PLATFORM_STATE', {
      key: 'districtOptions',
      value: data
    })
    dispatch('selectDistrictById', data[0].id)
  },
  async selectDistrictById({ commit }, id) {
    commit('SET_SELECTEDDISTRICTID', id)
    commit('SET_STATIONOPTIONS', await queryStationListById({ id }))
  },
  selectDistrictByName({ state, commit, dispatch }, name) {
    const selectedDistrict = state.districtOptions.find(option => option.name === name)
    if (selectedDistrict) {
      dispatch('selectDistrictById', selectedDistrict.id)
    }
  },
  selectStationById({ commit }, id) {
    commit('SET_SELECTEDSTATIONID', id)
    commit('MARK_CURRENTSTATION', id)
  }
}
export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions
}