<template>
  <div :class="className" :style="{height:height,width:width}" class="wrapper" />
</template>

<script>
import echarts from './echarts'
import { debounce } from '@/utils'
import { mapGetters } from 'vuex'
require('echarts/theme/macarons') // echarts theme
require('echarts/map/js/province/beijing')

export default {
  model: {
    prop: 'districtSelected',
    event: 'change'
  },
  props: {
    className: {
      type: String,
      default: 'chart'
    },
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '100%'
    },
    districtSelected: {
      type: String
    },
    stationOptions: {}
  },
  watch: {
    selectedDistrict: {
      handler (val) {
        this.$nextTick(() => {
          this.selectDistrict(val.name)
        })
      }
    },
    stationOptions: {
      handler(val) {
        if (val) {
          const marks = val.map(mark => {
            return {
              _id: mark.id,
              _name: mark.name,
              coord: [mark.longitude, mark.latitude]
            }
          })
          this.setOption({ marks })
        }
      }
    }
  },
  data() {
    return {
      chart: null
    }
  },
  computed: {
    ...mapGetters('platform', ['selectedDistrict'])
  },
  mounted() {
    this.__chartOption = {
      series: [{
        type: 'map',
        map: '北京',
        roam: true,
        label: {
          show: true,
          color: '#677f9e',
          fontSize: 10
        },
        selectedMode: 'single',
        itemStyle: {
          borderColor: '#4ea7d0',
          borderWidth: 1,
          shadowColor: 'rgba(78, 167, 208, 0.5)',
          shadowBlur: 5,
          areaColor: '#374464'
        },
        emphasis: {
          itemStyle: {
            borderColor: '#3c81e0',
            borderWidth: 1,
            shadowColor: 'rgba(60, 129, 224, 0.9)',
            shadowBlur: 5,
            areaColor: '#35416a'
          },
          label: {
            color: '#677f9e'
          }
        },
        markPoint: {
          symbol: 'diamond',
          symbolSize: 8,
          data: [{
            coord: [116.32715863448607, 39.990912172420714]
          }],
          itemStyle: {
            color: '#3fffff',
            shadowColor: 'rgba(55, 255, 255, 0.9)',
            shadowBlur: 5
          }
        }
      }]
    }
    this.$nextTick(() => {
      this.initChart()
      this.addMapHandler()
    })
    this.__resizeHandler = debounce(() => {
      if (this.chart) {
        this.chart.resize()
      }
    }, 100)
    window.addEventListener('resize', this.__resizeHandler)
  },
  beforeDestroy() {
    if (!this.chart) {
      return
    }
    window.removeEventListener('resize', this.__resizeHandler)
    this.chart.dispose()
    this.chart = null
  },
  methods: {
    initChart() {
      this.chart = echarts.init(this.$el, 'macarons')
      this.setOption()
    },
    setOption({ marks } = {}) {
      if (marks) {
        this.__chartOption.series[0].markPoint.data = marks
      }
      this.chart.setOption(this.__chartOption)
    },
    highlight(name) {
      if (this.chart && name) {
        this.chart.dispatchAction({
          type: 'downplay'
        })
        this.chart.dispatchAction({
          type: 'highlight',
          seriesIndex: 0,
          name
        })
      }
    },
    addMapHandler() {
      this.chart.on('click', params => {
        const { seriesType, name } = params
        this.highlight(this.selectDistrict.name)
        if (seriesType === 'map') {
          this.$emit('change', name)
        }
      })
    },
    selectDistrict(name) {
      this.chart.dispatchAction({
        type: 'mapSelect',
        seriesIndex: 0,
        name
      })
    }
  }
}
</script>
