<template>
  <div class="wrapper">
    <el-row style="height:500px" :gutter="0">
      <el-col :span="10" style=";padding-bottom:20px;">
        <v-card class="city_map">
          <!-- 地图选择区县下拉框 -->
          <div class="district-seletor">
            <div class="locator"></div>
            <el-select class="selector" :value="selectedDistrictID" popper-class="station-pop" @change="selectDistrictById">
              <el-option :label="d.name" :value="d.id" v-for="(d,i) in districtListForMap" :key="i"></el-option>
            </el-select>
          </div>
          <!-- 北京地图-->
          <city-chart height="350px" :district-selected="selectedDistrict.name" @change="selectDistrictByName" :station-options="stationOptions"/>
        </v-card>
      </el-col>
      <el-col :span="14">
        <!-- 区县地图 -->
        <district-chart class="district_map" height="400px" :district-selected="selectedDistrict.name" :station-options="stationOptions" @state-select="selectStationById"/>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'
import CityChart from '@/components/charts/CityChart'
import DistrictChart from '@/components/charts/DistrictChart'
import { tCard, vCard } from '@/components/cards/index'
export default {
  components: {
    CityChart,
    DistrictChart,
    tCard,
    vCard
  },
  created(){
    this.getDeptByHierarchy()
  },
  data(){
    return {
      districts: ['东城区', '西城区', '朝阳区', '丰台区', '石景山区', '海淀区', '门头沟区', '房山区', '通州区', '顺义区', '昌平区', '大兴区', '怀柔区', '平谷区', '密云区', '延庆区']
    }
  },
  methods: {
    ...mapActions('platform', ['selectDistrictById', 'selectStationById', 'selectDistrictByName','getDeptByHierarchy'])
  },
  computed: {
    ...mapState('platform', ['districtOptions', 'selectedDistrictID', 'stationOptions', 'selectedStationID']),
    ...mapGetters('platform', ['selectedDistrict']),
    districtListForMap() {
      return this.districtOptions.filter(i => this.districts.indexOf(i.name) !== -1)
    }
  }
}
</script>

<style lang="stylus">
html, body
  height 100%
  margin 0
  background radial-gradient(circle, #39638d, #34395f )
ol, ul
  list-style none
  padding 0
#app
  height 100%
  background-color #ddd
  font-family 'Avenir', Helvetica, Arial, sans-serif
  -webkit-font-smoothing antialiased
  -moz-osx-font-smoothing grayscale
</style>
<style lang="scss" scoped>
@import '@sass/variables.scss';
.wrapper{
  width: 800px;
  margin: 10px auto;
}
.district-seletor{
  height: 40px;
  margin-top: 15px;
  background: linear-gradient(to right, rgba(255,255,255,.0), rgba(111,141,194,.3),  rgba(255,255,255,.0));
}
.city_map{
  background: $panelBg;
}
</style>
<style lang="scss">
.district-seletor{
  position: relative;
  .locator{
    background-image: url('~@/assets/images/locator.png');
    background-size: 100% 100%;
    // background-color: #f00;
    width: 18px;
    height: 18px;
    position: absolute;
    top: 10px;
    right: 60%;
    z-index: 3;
  }
  .selector{ // el-select's classname
    width: 60%;
    margin: 0 auto;
    display: block;
    .el-input__inner{ // inner input element
      background-color: transparent;
      border: none;
      color: #5bb0e3;
      text-align: right;
      font-weight: bold;
      padding-right: 40px;
    }
  }
}

.warn-list{
  margin-top: 40px;
  font-size: 15px;
  .warn-list-item{
    margin-bottom: 7px;
    font-weight: bold;
    .title{
      color: #5fc1e9;
      display: inline-block;
      width: 110px;
    }
    .content{
      font-size: 16px;
      color: #fff;
      &.emphasis{
        color: #ff6533;
      }
    }
  }
}
</style>
