<template>
<!-- 首页完成\告警面版 -->
  <div class="card-wrapper" :style="styleObj">
    <div class="card-title" v-if="title">{{title}}</div>
    <span class="lt"></span>
    <span class="rt"></span>
    <span class="lb"></span>
    <span class="rb"></span>
    <slot/>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String
    },
    height: {
      type: String
    },
    width: {
      type: String
    },
    mstyle: {
      type: Object,
      default: () => ({})
    }
  },
  computed: {
    styleObj() {
      return Object.assign({
        width: this.width,
        height: this.height
      }, this.mstyle)
    }
  }

}
</script>

<style lang="stylus" scoped>
$borderColor=#5889ba
.card-wrapper
  position relative
  border 1px solid #3c4b6e
  border-radius 5px
  font-size 16px
  color #fff
  .card-title
    padding 8px
    padding-left 30px
  .lt,.rt,.lb,.rb
    position absolute
    width 12px
    height 12px
    z-index 5
  .lt
    top 0
    left 0
    border-top 1px solid $borderColor
    border-left 1px solid $borderColor
  .rt
    right 0
    top 0
    border-right 1px solid $borderColor
    border-top 1px solid $borderColor
  .rb
    right 0
    bottom 0
    border-right 1px solid $borderColor
    border-bottom 1px solid $borderColor
  .lb
    left 0
    bottom 0
    border-left 1px solid $borderColor
    border-bottom 1px solid $borderColor
</style>
